@mixin mobile {
    @media screen and (max-width: 480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width: 768px) {
        @content;
    }
}

@mixin laptop {
    @media screen and (max-width: 936px) {
        @content;
    }
}

.formContainer {
    font-family: 'Roboto';
    background-color: rgb(49, 147, 199);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.05rem;
    min-width: 360px;

    .formWrapper {
        background-color: rgb(240, 236, 228);
        padding: 1.5rem 3rem;
        border-radius: 0.5rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        align-items: center;

        .logo {
            color: rgb(49, 147, 199);
            font-weight: bold;
            font-size: 1.5rem;
        }

        .title {
            color: rgb(49, 147, 199);
            font-size: 1rem;
        }

        .error {
            color: tomato;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 1rem;

            .loading {
                color: tomato;
                align-self: center;
                font-size: 1rem;
            }

            input {
                padding: 0.6rem;
                border: none;
                border-bottom: 1px solid rgba(49, 147, 199, 0.3);
                background-color: rgb(240, 236, 228);

                &::placeholder {
                    color: rgba(0, 0, 0, 0.35)
                }
            }

            button {
                background-color: rgb(49, 147, 199);
                color: rgb(240, 236, 228);
                padding: 0.8rem;
                font-weight: bold;
                border: none;
                cursor: pointer;
                border-radius: 0.25rem;
            }

            label {
                display: flex;
                align-items: center;
                gap: 0.5rem;
                font-size: 0.8rem;
                color: rgb(49, 147, 199);
                cursor: pointer;

                img {
                    width: 1.8rem;
                }
            }
        }

        p {
            color: rgb(49, 147, 199);
            font-size: 0.8rem;
            margin: 0.5rem auto;
        }
    }
}

.home {
    font-family: 'Roboto';
    background-color: rgb(153, 207, 237);
    height: calc(100vh - 16px);
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.03rem;
    min-width: 360px;

    @include tablet {
        border-radius: 0.5rem;
    }

    @include mobile {
        border-radius: 1rem;
        height: 90vh;
    }

    .container {
        border-radius: 0.25rem;
        width: 85%;
        height: 90%;
        display: flex;
        overflow: hidden;

        @include laptop {
            width: 95%;
            height: 95%;
        }

        @include tablet {
            width: 95%;
            height: 96%;
            border-radius: 0.5rem;
        }

        @include mobile {
            width: 98%;
            height: 99%;
            border-radius: 1rem;
        }

        .sidebar {
            flex: 1;
            background-color: rgb(78, 156, 198);
            border-radius: 0.15rem;
            position: relative;

            @include mobile {
                min-width: 5rem;
            }

            .navbar {
                display: flex;
                align-items: center;
                background-color: rgb(9, 95, 141);
                height: 2rem;
                padding: 1rem 0.5rem;
                justify-content: space-between;
                color: rgb(244, 164, 16);

                .logo {
                    font-weight: bold;
                    font-size: 1.2rem;

                    @include laptop {
                        position: absolute;
                        right: 0.5rem;
                    }

                    @include mobile {
                        display: none;
                    }
                }

                .user {
                    display: flex;
                    gap: 1rem;
                    align-items: center;

                    img {
                        height: 1.8rem;
                        width: 1.8rem;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    span {
                        position: relative;
                        right: 0.5rem;
                        color: rgb(244, 164, 16);
                    }

                    button {
                        background-color: rgb(20, 74, 104);
                        color: rgba(244, 164, 16, 0.8);
                        font-size: 0.8rem;
                        padding: 0.25rem;
                        border: none;
                        border-radius: 0.2rem;
                        cursor: pointer;

                        @include laptop {
                            position: absolute;
                            bottom: 0.8rem;
                        }
                    }
                }
            }

            .search {
                position: relative;
                padding-bottom: 0.5rem;
                border-bottom: 1px solid rgba(255, 255, 255, 0.3);

                .notfound {
                    display: flex;
                    justify-content: center;

                    @include mobile {
                        text-align: center;
                    }

                }

                span {
                    position: relative;
                    right: 0.5rem;
                    color: rgba(244, 164, 16, 0.8);
                    margin: 0.5rem;
                    font-size: 0.9rem;
                    font-weight: 400;
                }

                .searchForm {
                    padding: 0.6rem;

                    input {
                        background-color: transparent;
                        border: none;
                        color: rgba(255, 255, 255, 0.7);
                        outline: none;

                        &::placeholder {
                            color: rgba(255, 255, 255, 0.7);
                        }
                    }
                }
            }

            .userChats {
                padding: 0.01rem 0.5rem;
                display: flex;
                align-items: center;
                gap: 0.5rem;
                color: rgba(0, 0, 0, 0.7);
                cursor: pointer;
                position: relative;

                @include mobile {
                    margin: 0.75rem 0;
                }

                &:hover {
                    background-color: rgb(9, 95, 141);
                }

                img {
                    width: 2rem;
                    height: 2rem;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .userChatInfo {
                    position: relative;
                    top: -0.5rem;

                    span {
                        position: relative;
                        top: 1rem;
                        font-size: 0.95rem;
                        font-weight: bold;

                        @include mobile {
                            top: 0.52rem;
                        }
                    }

                    p {
                        position: relative;
                        top: 0.4rem;
                        font-size: 0.75rem;
                        color: rgba(255, 255, 255, 0.7);

                        @include mobile {
                            display: none;
                        }
                    }
                }
            }
        }

        .chat {
            flex: 2.5;

            .chatInfo {
                display: flex;
                align-items: center;
                height: 2rem;
                padding: 1rem;
                background-color: rgba(94, 181, 227, 0.7);

                span {
                    position: relative;
                    left: 0.4rem;
                    color: black;
                    letter-spacing: 0.08ch;
                }

                img {
                    width: 1.8rem;
                    height: 1.8rem;
                    border-radius: 50%;
                    object-fit: cover;
                    outline: none;
                }
            }

            .messages {
                background-color: rgba(255, 255, 255, 0.7);
                height: calc(100% - 9rem);
                padding: 0.5rem;
                overflow: scroll;
                color: rgba(0, 0, 0, 0.7);

                .message {
                    display: flex;
                    gap: 1rem;
                    margin-bottom: 1.1rem;

                    .messageInfo {
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                        font-size: 0.8rem;

                        img {
                            width: 2rem;
                            height: 2rem;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    .messageContent {
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 0.5rem;

                        p {
                            background-color: rgba(255, 255, 255, 0.7);
                            padding: 0.5rem 1rem;
                            border-radius: 0 0.5rem 0.5rem 0.5rem;
                            max-width: max-content;
                        }

                        img {
                            width: 50%;
                            border-radius: 0.15rem;
                        }
                    }

                    &.owner {
                        flex-direction: row-reverse;

                        .messageContent {
                            align-items: flex-end;

                            p {
                                background-color: rgba(50, 125, 166, 0.5);
                                color: rgba(255, 255, 255, 0.8);
                                border-radius: 0.5rem 0 0.5rem 0.5rem;
                            }
                        }
                    }
                }
            }

            .input {
                height: 2rem;
                background-color: white;
                padding: 1rem;
                display: flex;
                align-items: center;
                justify-content: space-between;

                input {
                    width: 100%;
                    border: none;
                    outline: none;
                    font-size: 1.1rem;
                    font-weight: lighter;
                    color: rgba(6, 47, 70, 0.8);

                    &::placeholder {
                        color: lightgray;
                    }
                }

                .send {
                    display: flex;
                    align-items: center;
                    gap: 0.4rem;
                    position: relative;

                    img {
                        position: relative;
                        right: 0.5rem;
                        width: 1.5rem;
                        height: 1.5rem;
                        cursor: pointer;
                    }

                    button {
                        border: none;
                        padding: 0.5rem 0.5rem;
                        color: rgba(255, 255, 255, 0.7);
                        background-color: rgba(50, 125, 166, 0.8);
                        cursor: pointer;
                        border-radius: 0.15rem;
                    }
                }
            }
        }
    }
}